import React, { Component } from 'react'
import s from './css.less'

import Container from '@/component/container/Container'
import Bar from '@/component/bar/Bar'
import Panel from '@/component/panel/Panel'

const data = [
	{
		name: "selector",
		text: "CSS ",
		html: <div>
			<p>CSSCSS</p>
			<p>&nbsp;
				<a target="_blank"
					href="http://www.w3school.com.cn/css"
					rel="noreferrer noopener">http://www.w3school.com.cn/css</a>
			</p>

			<h4></h4>
			<p>type selector.</p>
			<p></p>
			<p> p</p>

			<h4>id</h4>
			<p>idid# #name idname</p>
			<p>idid</p>

			<h4></h4>
			<p>CSS</p>
			<p>. .name name</p>

			<h4></h4>
			<p></p>
			<p>[]</p>
			<p> [title] titletitle</p>
			<p> [title="hello"] titlehello</p>

			<h4></h4>
			<p></p>
		</div>
	},
	{
		name: "color",
		text: "CSS ",
		html: <div>
			<p>css color </p>
			<ul>
				<li> red</li>
				<li>RGB rgb(255,0,0)</li>
				<li>RGBA rgba(255,0,0,1)</li>
				<li>16RGB #ff0000  #f00</li>
				<li>16RGBA #ffff0000</li>
			</ul>
			<p> hsla(30, 100%, 50%, 0.6)</p>
		</div>
	},
	{
		name: "font",
		text: "CSS ",
		html: <div>
			<h4>font-family </h4>
			<p> CSS </p>
			<ul>
				<li> -  "Serif"  "Monospace"</li>
				<li> -  "Times"  "Courier"</li>
			</ul>
			<p>CSS  5 </p>
			<ul>
				<li>Serif </li>
				<li>Sans-serif </li>
				<li>Monospace </li>
				<li>Cursive </li>
				<li>Fantasy </li>
			</ul>
			<p></p>

			<h4>font-style</h4>
			<p></p>
			<ul>
				<li>normal - </li>
				<li>italic - </li>
				<li>oblique - </li>
			</ul>
			<p>font-style  normal italic  oblique  italic  oblique </p>
			<p>italicoblique</p>
			<p>italic  oblique  web </p>

			<h4>font-weight</h4>
			<p> bold </p>
			<p> 100 ~ 900  9 100 900  400  normal 700  bold</p>
			<p> bolder lighter </p>
		
			<h4>font-size</h4>
			<p> web </p>
			<p> HTML  &lt;h1&gt; - &lt;h6&gt;  &lt;p&gt; </p>
			<p>font-size </p>
			<p></p>
			<ul>
				<li></li>
				<li></li>
				<li></li>
			</ul>
			<p></p>
			<ul>
				<li></li>
				<li></li>
			</ul>
			<p> 16  (1em=16px)</p>
		</div>
	},
	{
		name: "text",
		text: "CSS ",
		html: <div>
			<p>CSScolor</p>
			<ul>
				<li>text-indent: </li>
				<li>text-align: </li>
				<li>word-spacing: </li>
				<li>letter-spacing: </li>
				<li>text-transform: </li>
				<li>text-decoration: </li>
				<li>white-space: </li>
			</ul>
		</div>
	},
	{
		name: "background",
		text: "CSS ",
		html: <div>
			<h4></h4>
			<p></p>
			<ul>
				<li>background-color: </li>
				<li>background-image: </li>
			</ul>

			<h4></h4>
			<p>CSS</p>
			<ul>
				<li>background-position: </li>
				<li>background-repeat: </li>
				<li>background-attachment: fixed</li>
			</ul>
		</div>
	}
]

// 
const getScrollPosition = (el = window) => ({
	x: el.pageXOffset !== undefined ? el.pageXOffset : el.scrollLeft,
	y: el.pageYOffset !== undefined ? el.pageYOffset : el.scrollTop
})

var scrollHandler;

export default class Css extends Component {

	constructor(props) {
		super(props)
		this.state = {
			scrollTop: true
		}
	}

	componentDidMount() {
		scrollHandler = this.onScroll.bind(this)
    	window.addEventListener('scroll', scrollHandler)
    }

    componentWillUnmount() {
    	window.removeEventListener('scroll', scrollHandler)
    }

    onScroll(e) {
		if (getScrollPosition().y === 0) {
			if (!this.state.scrollTop)
				this.setState({ scrollTop: true })
		} else {
			if (this.state.scrollTop)
				this.setState({ scrollTop: false })
		}
	}

	handleLink(link) {
		var e = document.getElementById(link)
		if (e && e.scrollIntoView) {
			e.scrollIntoView()
			window.scrollBy(0, -70)
		}
	}

	render() {
		return <Container className={s.container}>
			<Bar className={s.bar} position={this.state.scrollTop ? '0' : '-3.3rem'}>
				<Panel title="CSS ">
					<ul>
					{
						data.map((item, key) => {
							return <li key={key}
								onClick={() => { this.handleLink(item.name) }}>
									{item.text}
							</li>
						})
					}
					</ul>
				</Panel>
			</Bar>
			<div className={s.main}>
				<dl>
				{
					data.map((item, key) => [
						<dt key={key} id={item.name}>{item.text}</dt>,
						<dd key={-key-1}>
							{
								item.html ?
									item.html :
									item.info.map((text, k) => <p key={k}>{text}</p>)
							}
						</dd>
					])
				}
				</dl>
				<div className={s.footer}>
					&nbsp;
					<a target="_blank"
						href="http://www.w3school.com.cn"
						rel="noreferrer noopener">w3school</a>
				</div>
			</div>
		</Container>
	}
}


